<template>
  <section class="pc-container pb-4">
    <div class="pcoded-content">
      <div class="row mb-4">
        <h5 class="text-dark fw-bold">Busqueda de expedientes</h5>
        <!-- <div class="col-md-3">
          <select class="form-control" v-model="filtro_tipo_expediente" disabled>
            <option value="">Tipo de expediente</option>
            <option>Civil/Laboral</option>
            <option>Penal</option>
            <option>Arbitral</option>
            <option>Indecopi</option>
          </select>
        </div>
        <div class="col-md-3">
          <select class="form-control" v-model="filtro_tipo_procesal">
            <option value="">Tipo Procesal</option>
            <option>DEMANDANTE</option>
            <option>DEMANDADO</option>
            
          </select>
        </div>
        <div class="col-md-3">
          <select class="form-control" v-model="filtro_materia">
            <option value="">Materia</option>
            <option v-for="(materia, index) in materias" :key="index" :value="materia.mat_nombre">
              {{ materia.mat_nombre }}
            </option>
          </select>
        </div>
        <div class="col-md-3">
          <select class="form-control" v-model="filtro_pretension">
            <option value="">Pretension</option>
            <option v-for="(pretension, index) in pretenciones" :value="pretension.pre_nombre" :key="index">{{
              pretension.pre_nombre }}</option>
          </select>
        </div> -->
      </div>
      <div class="row">
        <div class="col-xl-12">
          <div class="card">
            <div class="card-header">
              <h5 class="text-secondary">Lista de expedientes Civil/Laboral</h5>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <table class="table" id="table" ref="myDataTable">
                  <thead>
                    <tr>
                      <th>#</th>
                      <th>N째 de Expediente</th>
                      <th>Fecha de Inicio</th>
                      <th>Parte procesal</th>
                      <th>Materia/Delito</th>
                      <th>Pretensi처n</th>
                      <th>Estado</th>
                      <!-- <th class="text-center">Interacciones</th> -->
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(expediente, index) in expedientesFiltrados" :key="index">
                      <td>{{ index+1 }}</td>
                      <td>
                        <router-link
                          :to="{ name: 'expediente/detalle', params: { id: expediente.exp_id } }">
                          {{ expediente.numero }}
                        </router-link>
                      </td>
                      <td>{{ expediente.fecha_inicio }}</td>
                      <td>{{ isDemandante(expediente.procesal, expediente.multiple) }}</td>
                      <td>{{ expediente.materia }}</td>
                      <!-- <td>{{ isDemandado(expediente.procesal, expediente.multiple) }}</td> -->
                      <td>{{ expediente.pretencion }}</td>
                      <td>
                        <span :class="badgeClass(expediente.estado_proceso)">
                          {{ expediente.estado_proceso }}
                        </span>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
  
<script>
import feather from 'feather-icons';
import DataTable from '@/utils/dataTable';
import { badgeClass } from '@/utils/utils.js';
export default {
  name: 'Buscar',
  mixins: [DataTable],
  mounted() {
    this.$nextTick(() => {
      feather.replace();
    });
    this.cargarExpedientes();
    this.listarMaterias();
    this.listarPretensiones();
  },

  data() {
    return {
      filtro_tipo_expediente: '',
      filtro_tipo_procesal: '',
      filtro_materia: '',
      filtro_pretension: '',
      materias: [],
      pretenciones: [],
      expedientes: [],
      dataTable: null,
      exp: {},
      idExpediente: null,
      showModal: "",
      pretension: ""
    };
  },

  computed: {
    expedientesFiltrados() {
      return this.expedientes.filter((fila) => {
        return (
          (!this.filtro_tipo_expediente || fila.estado_proceso.includes(this.filtro_tipo_expediente)) &&
          (!this.filtro_tipo_procesal || fila.procesal[0].tipo_procesal.includes(this.filtro_tipo_procesal)) &&
          (!this.filtro_materia || fila.materia.includes(this.filtro_materia)) &&
          (!this.filtro_pretension || fila.pretencion.includes(this.filtro_pretension))
        );
      });
    },
  },

  methods: {
    async listarMaterias() {
      try {
        const result = await this.$getData('subject', this);
        this.materias = result.data;
      } catch (error) {
        this.$handleApiError(error, 'listar materias');
      }
    },
    async listarPretensiones() {
      try {
        const result = await this.$getData('claim', this);
        this.pretenciones = result.data;
      } catch (error) {
        this.$handleApiError(error, 'listar pretensiones');
      }
    },
    async cargarExpedientes() {
      try {
        this.$showLoadingAlert();
        const result = await this.$getData('proceeding', this);
        this.expedientes = result.data;
        console.log(result);
        await this.$waitForDataTableDraw(this, 'myDataTable');
        this.$closeLoadingAlert();
      } catch (error) {
        this.$handleApiError(error, 'cargar expedientes');
      }
    },
    isDemandante(procesal, multiple) {
      return procesal[0].tipo_procesal === 'DEMANDANTE'
        ? this.nombreCompleto(procesal, multiple)
        : 'UNPRG';
    },
    isDemandado(procesal, multiple) {
      return procesal[0].tipo_procesal === 'DEMANDADO'
        ? this.nombreCompleto(procesal, multiple)
        : 'UNPRG';
    },
    nombreCompleto(procesal, multiple) {
      if (multiple === '1') {
        return 'Demanda Colectiva';
      }

      const tipoPersona = procesal[0].tipo_persona;
      const { apellido_paterno, apellido_materno, nombres, razon_social } = procesal[0];

      return tipoPersona === 'NATURAL'
        ? `${apellido_paterno} ${apellido_materno} ${nombres}`
        : razon_social;
    },
    badgeClass,
  },
};
</script>
  
<style scoped>
@import '@/assets/css/fileStatus.css';
@import '@/assets/css/dataTable.css';

.container-div {
  position: relative;
}

.content-events {
  position: absolute;
  bottom: 5em;
  right: 5em;
  background-color: #fff;
  padding: 10px;
  border: 1px solid #ccc;
  z-index: 1;
}

.bg-links {
  background: #e3e1fc;
  color: #7267EF;
  border: 1px solid #e3e1fc;
  margin: 2.5px !important;
}
</style>
  